
export default class Particles {
    constructor(x, y,ctx,canvas,obj) {
        this.x = x;
        this.y = y;
        // this.radius = 15;
        this.weight = Math.random() * 1 + 1;
        this.size = Math.random() * 5 + 1;
        this.directionX = 2;
        this.ctx = ctx;
        this.canvas = canvas;
        this.titleObj = obj;
    }
    draw() {
        this.ctx.fillColor = "purple";
        this.ctx.beginPath().createArc(this.x, this.y, this.size, 0, 360).fill().closePath();
    }
    update() {
        // this.ctx.clearRect(0, 0, canvas.width, canvas.height);
        // this.draw();
        this.weight += 0.01;
        this.x -= this.directionX;
        this.y += this.weight;
        if (this.y > this.canvas.height) {
            this.weight = 2;
            this.y = 0 - this.size;
            this.x = Math.random() * this.canvas.width;
            this.directionX = 2;
        }

        if (this.x < this.titleObj.x + this.titleObj.width &&
            this.x + this.size > this.titleObj.x &&
            this.y < this.titleObj.y + this.titleObj.height &&
            this.y + this.size > this.titleObj.y) {
                this.y -= 3;
                this.weight *= -0.4;
        }
    }
}