<template>
  <div class="position-relative main">
    <div id="title" ref="textBox">javascript</div>
    <!-- <button onclick="alert('hello')">click me</button> -->
    <canvas id="canvas" ref="test" width="500" height="500"></canvas>
  </div>
</template>
<script>
import Particles from '../service/rain';
import Ccanvas from '../service/canvas';
export default {
    mounted(){
        this.canvas = new Ccanvas();
        this.board = this.$refs.test;
        this.context = this.canvas.createCanvas(this.board,'2d');
        this.init();
    },
    data(){
        return {
            particlesArray:[],
            numberOfParticles :200,
            canvas: null,
            context: null,
            board:null
        }
    },
    methods:{
        init() {
                // this.board.width = window.innerWidth;
                // this.board.height = window.innerHeight;
                const title = this.$refs.textBox;
                console.log(title.offsetTop)
                let titleDimensions = title.getBoundingClientRect();
                const titleObj = {
                    x: title.offsetLeft,
                    y: title.offsetTop,
                    width: titleDimensions.width,
                    height: titleDimensions.height,
                }
                for (let index = 0; index < this.numberOfParticles; index++) {
                    this.particlesArray.push(new Particles(Math.random() * this.board.width, Math.random() * this.board.height, this.context,this.board,titleObj));
                }
                this.animation();
                },
    animation() {
        // this.context.clearRect(0,0,this.board.width,this.board.height)
        this.context.fillColor = 'rgba(255,255,255,0.01)';
        this.context.fillRect(0, 0, this.board.width, this.board.height);
        this.particlesArray.forEach(d => {
            d.update();
            d.draw();
        });
        requestAnimationFrame(this.animation);
    }
}
}
</script>

